import {api} from '../../api/api.js'
import AdItem from './child/AddItem.vue'
export default {
	data() {
		return {
			windowWidth: getApp().globalData.windowWidth,
			windowHeight: getApp().globalData.windowHeight,
			top: getApp().globalData.top,
			session_id:getApp().globalData.session_id,
			uid:getApp().globalData.uid,
			list:[],
			total_page:0
		}
	},
	onLoad() {
		this.getAddressList()
	},
	onShow() {
		this.getAddressList();
	},
	methods: {
		remove(id) {
			uni.request({
				url:api.remove,
				data: {
					uid:this.uid,
					session_id:this.session_id,
					id:id
				},
				method:"post",
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: (res) => {
					console.log(res.data);
					if(res.data.code === "success") {
						uni.showToast({
							title:res.data.msg,
							icon:"none"
						})
					}else  {
						uni.showToast({
							title:res.data.msg,
							icon:"none"
						})
					}
				}
			})
			this.getAddressList();
			this.$forceUpdate();
		},
		back() {
			uni.navigateBack();
		},
		addAddress() {
			uni.navigateTo({
				url:'../AddAddress/AddAddress'
			})
		},
		getAddressList() {
			uni.request({
				url:api.address_list,
				data: {
					uid:this.uid,
					session_id:this.session_id
				},
				method:"post",
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: (res) => {
					this.total_page = res.data.total_page;
					console.log(res.data);
					if(res.data.code === "success") {
						this.list = res.data.list;
					}else  {
						/* uni.showToast({
							title:res.data.msg,
							icon:"none"
						}) */
					}
				}
			})
		}
	},
	components:{
		AdItem
	}
}
